.banner {
  background-color: rgba(93, 26, 74, 1);
  text-align: center;
  justify-content: center;
  align-items: center;
  font-size: 2.4vw;
}

.img-home {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.p_text {
  font-size: 0.5vw;
}

h1 {
  font-size: 2.5vw;
}

.banner_bottom {
  overflow: hidden;
  display: flex;
}

.img_banner {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}

.text_gif {
  position: absolute;
  bottom: 0;
  text-align: center;
  color: var(--Texto);
  z-index: 1;
}

.item1 {
  grid-area: area1;
}

.item2 {
  grid-area: area2;
}

.item3 {
  grid-area: area3;
 justify-content: center;
 align-items: center;
 background-color: aliceblue;
}

.item4 {
  grid-area: area4;
}

@media (max-width:900px) {
  .banner {
    font-size: 15px;
  }

  h1 {
    font-size: 20px;
    line-height: 20pt;
  }

  .p_text {
    font-size: 12px;
    line-height: 12pt;
  }
}